/*PIPE: sp xp array string (chui): nh dng vit hoa cc k t u ca t, K C k t c du nh , , ...
(pipe titlecase c sn: vit hoa k t u ca ch, nhng n KHNG nh dng c cc k t c du nh , , ...)
*/

import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'capitalize'
})
export class CapitalizePipe implements PipeTransform {

  /*Ct t: dng hm split() gip ct chui da vo k t no ,  y ct t nn ct da vo khong trng ' '.
    ct xong s tr v mng cha cc t ca chui.

  */
  transform(value: any, args?: any): any {
    var arrStr = value.split(' '); //hm split() ct chui da vo k t no ,  y l space ' '
    var result = '';


    /* vit hoa ch u ca t:
    -dng hm split() ct tng t ra, tr v 1 mng cha cc t
    -forEach duyt mi t:
    +str.charAt(0): k t u tin ca t (upperCase n ln)
    +str.substr(1): ct cc k t t v tr k t 1 n ht t .
    
    =>cng dn 2 ci str.charAt(0).toUpperCase() + str.substr(1): s ra c 1 t hon chnh c vit hoa k t u.

    +index !== arrStr.length - 1: x l du cch, nu ko phi t cui cng th chn du cch ' ' vo.
    */
    arrStr.forEach((str, index) => {
      // console.log(index);
      // console.log(arrStr.length);

      //kim tra kdl 2 thng ny u l number, nn dng c ===,!== (so snh cng kiu dl). 
      // console.log(typeof index);
      // console.log(typeof arrStr.length);

      //Xem gii thch  phn trn
      result += str.charAt(0).toUpperCase() + str.substr(1) + (index !== arrStr.length - 1 ? ' ' : '');
    });
 
    console.log(result);

    return result;
  }

}
