import { Pipe, PipeTransform } from '@angular/core';

//NH VO NOTE BI 73  XEM CHI TIT V LODASH: https://lodash.com/docs/4.17.10#orderBy
//lodash l th vin h tr cc tin ch cho pipe: Vo note bi 73  xem chi tit,  y ta vn dng lodash vo sp xp.

/*Lodash: CCH 1
terminal trong prj: npm install lodash --save v gi n ra bng lnh import bn di.
-cch 1: l import tt c cc hm ca lodash, nn phi sd _.tn_hm()  gi hm; v phi khai bo cho _
import 'lodash';
declare var _; // s dng c cu trc hm _.tn_hm() ca lodash th phi khai bo _ cho js
=>Xong r xung di gi tn hm ra sd: _.tn_hm() vd: _.orderBy()
*/
// import 'lodash';
// declare var _; 


/*Cch 2 ca LODASH: terminal trong prj: npm install @types/lodash --save
-cch 1: l import tt c cc hm ca lodash, nn phi sd _.tn_hm()  gi hm; v phi khai bo cho _
-cch 2: sd hm no import hm   thi, vy nn c th gi trc tip tn_hm() m ko cn _.
nn ko cn khai bo declare var _;

ch cn: import { orderBy } from 'lodash'; 
=>xong ri xung di gi tn hm ra sd: tn hm ko cn du _: tn_hm() vd: orderBy() 

CCH 2 DNG GIP LM NH PRJ HN, TNG PERFORMANCE HN.
*/
import { orderBy } from 'lodash';


@Pipe({
  name: 'sort'
})
export class SortPipe implements PipeTransform {

  transform(products: any[], sortBy : string, sortValue: number): any {

    /*Ban u mi render ln th sortBy='name' v sortValue=1  l mc nh.
    V sortValue=1  nn s theo cu trc: a>b tr 1, a<b tr -1, a===b tr 0: l cu trc sx tng dn ca hm sort();
    =>vy nn ban u khi mi va render ln th  sx tng dn theo name.
 
    -Ln click u tin Name/Price: Khi kch vo tiu  no  th n s lm thay i sortValue=-sortValue=-1, 
    lc ny s theo cu trc: a>b tr -1, a<b tr 1, a===b tr 0: l cu trc sx gim dn ca hm sort();
    =>vy nn ln click ln u tin bt c ci no th cng s chuyn sang sx gim dn.
    */
   
    if(sortBy === 'name'){
      // nu =name th ta sx theo name.
      // products.sort((a, b) =>{
      //   return a.name > b.name ? sortValue : (a.name < b.name ? -sortValue : 0);
      // });
      
      //S dng lodash  sx orderBy thay v t lm nh trn: vo note bi 73  xem gii thch.

      // Cch 1 ca lodash:
      // products = _.orderBy(products, ['name'], [sortValue === 1 ? 'asc' : 'desc']);
 
      //Cch 2 ca lodash: v import trc tip nn ko cn gi hm bng du _.
      products = orderBy(products, ['name'], [sortValue === 1 ? 'asc' : 'desc']);

      /*nu sortValue===1 th sx name theo tng dn (asc), v nu sortValue !==1 th sx name theo gim dn (desc).
      sortValue: l gi tr nhn vo 1/-1 nu ta kch sx tng hay gim...*/
    }

    if(sortBy === 'price'){
      // nu =price th ta sx theo price.
      // products.sort((a, b) =>{
      //   return a.price > b.price ? sortValue : (a.price < b.price ? -sortValue : 0);
      // });

      //S dng lodash  sx orderBy thay v t lm nh trn: vo note bi 73  xem gii thch.
     
      // Cch 1 ca lodash:
      // products = _.orderBy(products, ['price'], [sortValue === 1 ? 'asc' : 'desc']);

      //Cch 2 ca lodash: v import trc tip nn ko cn gi hm bng du _.
      products = orderBy(products, ['price'], [sortValue === 1 ? 'asc' : 'desc']);

      /*nu sortValue===1 th sx price theo tng dn (asc), v nu sortValue !==1 th sx price theo gim dn (desc)
      sortValue: l gi tr nhn vo 1/-1 nu ta kch sx tng hay gim...*/
    }

    return products;
  }

}
