import { Component, OnInit } from '@angular/core';

//import  s dng service trong weather.service.ts: Xem ip2.component.ts c gii thch r
//import  y vo gi ra trong providers[]
import { WeatherService } from '../../folder-services/weather.service';


@Component({
  selector: 'app-weather',
  templateUrl: './weather.component.html',
  styleUrls: ['./weather.component.css'],

  //gi service vo comp
  //nu import v gi service trong comp ny th ch mi comp ny c sd service, comp khc mun sd phi import service li nh v.
  //Nu import v gi service trong app.module.ts, th service s c dng cho mi comp. 
  providers: [WeatherService]
})

export class WeatherComponent implements OnInit {

  txtCityName=''; // bn html dng ngModel  gn vi bin ny. Nhp vo l n t nhn dl.
  cityName = ''; // bin thnh ph hin th ra = tn tp tm kim
  temp= null; //nhit , lc u cha c nhit  nn cho null
  isLoading= false;//thuc tnh hin th ang loading..., mi u cha load g c nn false.

  //khi to ngm object ca service cn gi
  //C ch Dependence Injection s Inject service vo constructor, m ko cn phi khi to i tng.
  constructor(private weatherService: WeatherService) { }

  //test th service get weather
  // ngOnInit() {
  //   this.weatherService.getTemp('Tokyo') //gi hm getTemp ca service, truyn tham s vo.
  //   .then(temp => console.log(temp))
  //   .catch(err => console.log(err));
  // }

  ngOnInit(){
  }

  getWeather(){
    //khi va bt u get th cho hin trng thi loading... ln.
    this.isLoading = true;

    this.weatherService.getTemp(this.txtCityName) //gi hm getTemp ca service, truyn tham s vo.
    .then(temp => {
      this.cityName = this.txtCityName; //gn li tn thnh ph hin th ra = tn tp tm kim
      this.temp = temp; //gn bin nhit  = nhit  tm c
      
      //khi ly d liu xong r, b hin th trng thi loading...; v gn li  nhp thnh rng.
      this.isLoading = false;
      this.txtCityName = ''; //gn rng li cho  nhp thnh ph vo
    })
    // .catch(err => console.log(err)); //in li ra console.
    .catch(err => {
      alert('Cannot find your city!'); //hin th thng bo li, nhn ok
      this.isLoading = false; //sau khi nhn ok th b trng thi ang loading...
      this.cityName = ''; //gn li = rng,  ch hin th thnh ph li hin "Enter your city name"
      this.txtCityName = ''; //gn rng li cho  nhp thnh ph vo
    });
  }

  //getmessage() hm show d liu ra cho ngi dng. Thay v vit trong html di dng th ta vit  y r gi qua html.
  //nu ang ly dl th hin th loading...
  getMessage(){
    if(this.isLoading){
      return 'Loading...';
    }
    return this.cityName === '' ? 'Enter your city name' : (this.cityName + ' is now ' + this.temp);
  }

}
