import { Component, OnInit } from '@angular/core';
import { Ip2Service } from '../../folder-services/ip2.service'; //IpService: l class bn trong ip.service.ts

@Component({
  selector: 'app-ip2',
  templateUrl: './ip2.component.html',
  styleUrls: ['./ip2.component.css'],

  // trn  import ri th y gi file ip.service.ts vo trong Component,  s dng n. 
  // providers: [IpService]  

  /* thuc tnh providers: l mng cha cc file c import.  y l ip.service.ts.
  -Nu ta gi providers  y: th ch mt comp ip2.comp.ts ny s dng c service trong file ip.service.ts
  (cc comp khc mun sd n cng phi lm tng t)

  -Nhng nu ta gi khai bo trong tp khai bo chung: trong app.module.ts, th mi comp u s dng c 
  service trong ip.service.ts . (cc comp khc mun sd th ch cn import n l ).

  +vo trong app.module.ts: import { IpService } from './ip2/ip.service';
  +cng ti app.module.ts: ko xung gi IpService vo trong providers l xong: providers: [IpService]
  */
})

/*Xem trc bi 35 trong folder ip: ip.component.ts
----------------------
Bi 36: Cch 2: Vit ring service,  comp tp trung vo vic iu khin cc view. Vit ring cn thun li cho vic testing.
To folder ip2.component.ts  thc hin get service tch ring.

-To file ip.service.ts: file ny cha service: qua file   xem gii thch chi tit.
-V dng n file ip.service.ts nn import n vo.

-trong @component: thm vo mng providers  cha cc file import qua  s dng n trong comp ny,
 y n cha file IpService.

-Trong class comp: nhn ip tr v.
*/
export class Ip2Component implements OnInit {
  
  ip: String;

  //bin: kdl,  y l kb bin ipService
  //C ch Dependence Injection s Inject service vo constructor, m ko cn phi khi to i tng.
  constructor(private ipService: Ip2Service) { 
    // this.ipService.getIp()  
    // .then(ip => this.ip = ip) 
    // .catch(err => console.log(err));
  }

  //Xem phn di s gii thch ti sao dng ngOnInit()  gn ip, thay cho dng trong hm to.
  ngOnInit(): void {
    this.ipService.getIp()  //gi ti hm getIp(), hm ny ly ip t server v, ri x l kiu dl, ri tr v ip.
    .then(ip => this.ip = ip) //then: s nhn ip tr v .
    .catch(err => console.log(err));
  }

  /*hm ngOnInit(): l hm chy ngay sau khi constructor c khi to, l thi im thch hp  ly 
  dl trn server hoc ly dl cc thuc tnh comp. (lun t bn di hm to)

  Mun dng phi import OnInit t core, ri override li hm ngOnInit() theo  mun.
  (bt khi a-component  to comp th  c  sn ngOnInit() ri.)

  -Vy nn  y ta ko ly ip (t IpService-ip.service.ts)  trong hm to na, m a vo hm ngOnInit()  ly ip. 
  Hm to ch cn khi to i tng ca IpService l c.
  */ 
}
