import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';


@Component({
  selector: 'app-sign-up3',
  templateUrl: './sign-up3.component.html',
  styleUrls: ['./sign-up3.component.css']
})

/*Reactive Forms: https://angular.io/guide/reactive-forms ---- Bi 56: Validate trong Reactive form

Xem li cc bi 45 v 48:
Vo bi  sign-in.component.html v sign-in2.component.html  xem vd v cc dng validate

Xem bi 48: sign-in2.component.html: th ta s thy c gii thch v li required v touched khi kch vo ko nhp/nhp ko ng r kch ra, 
s thy  cu hnh trong file styles.css nn s dng chung cho mi  nhp khc c s dng required.

*/


/* validate trong reactive form th phi:   
-import Validators trong '@angular/forms' (nh chn Validators c s)
*/

// Bi ny ang  dng FormBuilder, ta thm validator cho n bng cch thm vo ch khai bo cc params
export class SignUp3Component implements OnInit {

  formSignUp: FormGroup; 

  constructor(private fb: FormBuilder) {}

  ngOnInit() {
    this.formSignUp = this.fb.group({  
        /* y s dng validator required: nu li required=true th tr v invalid=true v valid=false, lc  bn html s b n btn submit */

        //c th .email(li nh dng email), maxlenght, minlength... cc loi validate xem trong https://angular.io/guide/reactive-forms 
        // email: ['', Validators.required],  
        // email: ['', Validators.email],  
        // email: ['', gmailValidator],  //TEST VI VALIDATOR T VIT BN DI

        //TH s dng nhiu validator th  trong []
        email: ['', [Validators.email, Validators.required, gmailValidator] ],  
        password: '',
        subject: this.fb.group({
          nodeJS: false, 
          angular: true,
          reactJS: false
        }),
      });
  }

  onSubmit(){
    console.log(this.formSignUp.value);
  }

}

//vo y  xem cc dng validator c sn: https://angular.io/guide/reactive-forms 
//T vit 1 validator: vd: email phi c chui @gmail.com

/*Bn cht validator l vit 1 function thi. 
-truyn vo tham s: i din cho formControl hoc formGroup, t tn g cng c nhng nn t ng vi  ngha ca n.
nu vit validator cho formControl th t tn tham s truyn vo l formControl, ngc li cho formGroup th t formGroup

*/

function gmailValidator(formControl: FormControl){ //khai bo bin formControl vi kdl FormControl, c th (formControl) ch kb bo bin, ko cn kdl
  if(formControl.value.includes('@gmail.com')){ //hm includes() kim tra xem 1 chui c cha chui khc ko
    return null; //validator tr v null khi ko c li g 
  }
  
  //ngc li s tr v c li. Tr v object{ chc li hin ra trong ny }
  //to ra bin gmail gn cho n gtri true, ri return v bin gmail .
  return { gmail : true};
}
