import { Component, OnInit } from "@angular/core";

@Component({
  selector: "app-words",
  templateUrl: "./words.component.html",
  styleUrls: ["./words.component.css"]
})
export class WordsComponent implements OnInit {

  //to ra cc bin rng,  bn html s dng [(ngModel)]  gn d liu cho cc bin ny, mi ln nhp vo n t gn dl.
  newEn = '';
  newVn = '';

  filterStatus='XEM_TAT_CA';

  isShowForm = false;


  //thm mng object  dng ngFor
  //d liu ly t: https://github.com/vanpho93/learn-angular/blob/master/snippet.txt
  arrWords = [
    { id: 1, en: "action", vn: "hnh ng", memorized: true },
    { id: 2, en: "actor", vn: "din vin", memorized: false },
    { id: 3, en: "activity", vn: "hot ng", memorized: true },
    { id: 4, en: "active", vn: "ch ng", memorized: true },
    { id: 5, en: "bath", vn: "tm", memorized: false },
    { id: 6, en: "bedroom", vn: "phng ng", memorized: true }
  ];

  
  constructor() {}

  ngOnInit() {}

  //hm thm phn t vo mng.
  //nu mun thm vo cui th .push, cn mun thm vo u th .unshift
  addWord(){
    this.arrWords.unshift({
      id: this.arrWords.length + 1,
      en: this.newEn,   //this.newEn l gi ln thuc tnh newEn=''  trn. Thuc tnh ny c gn vi [(ngModel)] nn s nhn c cc dl nhp vo.
      vn: this.newVn,
      memorized: false
    });

    //sau khi add vo th xa trng li cc . V newEn c gn vi [(ngModel)], nn dl 2 ci s n theo vi nhau. 
    //lc ny ch cn ta cho newEn, newVn v li rng th lp tc [(ngModel)] cng thnh rng v  nhp vo s b rng theo.
    this.newEn = '';
    this.newVn = '';

    // Sau khi thm th n form i bng cch set li isShowForm=false. Cmt li v   y cng c,  qua bn button ca html cng c.
    //this.isShowForm=false; //qua button thm ca html  set li gi tr isShowForm cng c.
  }


  //hm xa phn t, nhn id t html truyn vo.
  //T id, tm index ca phn t , ri xa theo index.
  //Dng hm splice(startnumber, countnumber): TRong mng xa bt u t phn t bao nhiu v xa i bao nhiu phn t.
  //  y bt u t v tr index, v xa 1 phn t.
  removeWord(id: number){
    const index = this.arrWords.findIndex(word => word.id === id);
    this.arrWords.splice(index,1);
  }


  /*Vit hm lc danh sch.
  */
 getShowStatus(memorized: boolean){
    const dkXemTatCa = this.filterStatus==='XEM_TAT_CA'; //3 ci u tr v true/false
    const dkXemDaNho = this.filterStatus==='XEM_DA_NHO' && memorized; //memorized ngha l memorized===true 
    const dkXemChuaNho = this.filterStatus==='XEM_CHUA_NHO' && !memorized; 
    
    return dkXemTatCa || dkXemDaNho || dkXemChuaNho; //vit tt ca return dkXemTatCa nu dkXemTatCa===true,...    
 }

}
