import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

// Dng lnh ng g c person  to component person c y  4 file.

@Component({
  selector: 'app-person',
  templateUrl: './person.component.html',
  styleUrls: ['./person.component.css']
})
export class PersonComponent implements OnInit {

  /* nhn input truyn vo t app.component.html th phi:
  import Input vo dng import  trn cng.
  Khi khai bo cc bin  ly input th phi @Input() bin: kdl;
  */ 

  /*string: kdl bnh thng. Kiu ny ta c th so snh 2 chui === nhau.

String: object. L kiu hn hp. Kiu ny ta ko th so snh 2 chui c, v ngoi gi tr 2 chui cn cc thuc tnh khc.
(ko th so snh object nh so snh kdl bnh thng)
  */

  /*ng dng Output vo  gi s kin t person.ts qua list-person.ts: Xem cc file child.comp.ts  hiu r hn v Output.
    -Import EventEmitter, Output  dng trn cng
    -Khai bo s kin phi c @Output eventname = new EventMiter<kiu dl tham s truyn t y qua list-person.html>();.
    -eventname.emit(tham s truyn i):  kch hot s kin truyn i, ng thi gi param truyn i .

    -event removePerson  c to  y, qua list-person.html ta ch cn gi n event removePerson: <app-person (removePerson)="removePersonByName($event)"></app-person>
        +person.comp.ts gi (removePerson) n list-person.comp.html, nhng lc ny removePerson cha c tc dng v cha c kch hot.
        +ti person.comp.html, mi ln kch btnXa th event removePerson li c kch hot(gi n removePerson.emit(name)), 
        nn (removePerson) bn list-person mi c tc dng, mi hot ng.

    =>Tm li:  y html bm Xa th kch hot event removePerson, lm cho event removePerson  c gi bn list-person.html c tc dng, s thc thi s kin .

    -Ti list-person.html: ( <app-person (removePerson)="removePersonByName($event)"></app-person> ): V cha bit param l g, nn ta lun  l $event. 
      +(removePerson): l s kin output c gi t person.ts qua, s kin ny gi thm param qua, param ny c a vo $event.
      +gi param qua bng cch removePerson.emit(param truyn qua);
      + y param truyn qua l chui, nn  tin cho vic so snh th param truyn vo phi l string ch ko phi String (v String l object kh so snh).
      -->
  
  */


  //khai bo cc bin  nhn input truyn vo t app.comp.html(comp cha)
/* bn app.comp.html s gi n comp person v truyn dl vo comp person ny:
  <app-person name="To Nguyn" age="18"></app-person>
  <app-person name="T Nguyn" age="15"></app-person>

  -ti comp person.ts ta ch cn import, ri khai bo bin nh 2 dng bn di  nhn dl truyn vo.
*/

  /* C 2 cch t tn bin input: Vi keyname l tn input c gi  comp cha
  (nu comp cha mun gi ti input ny th n s gi keyname v gn gi tr vo keyname)

  +@Input() tenbienInput: kdl;  Cch ny th tenbienInput === keyname

  +@Input('keyname') tenbienInput : kdl;  Cch ny th tenbienInput c th !== keyname

  */

  //Dng cch truyn bng keyname
  // @Input('name') Name : string = 'ABC'; 

  //C th gn gi tr default hoc ko. Gi tr default s c dng nu nh input ko c truyn gi tr vo.
  @Input() name: string = 'Tn Cn Trng';
  @Input() age: Number;


  @Output() removePerson = new EventEmitter<string>();

  constructor() { }

  ngOnInit() {
  }

  removeByClick(){
    this.removePerson.emit(this.name);
  }

}
