import { Component, OnInit, ViewChild } from '@angular/core';
import { ChildComponent } from '../child/child.component';


@Component({
    selector: 'app-parent',

    //$event nhn tham s truyn qua t child.ts: qua  xem.

    /*Bai 31: To object. Thay i bin thng qua object:
    
    #child trong <app-child #child>: l to ra 1 bin/object child ca class child.comp.ts.
        Do  child c th gi n bin value2  child.comp.ts  set li gi tr cho n.

        Vy khi click btn Add for Child th bin child s gi n value2  child.comp.ts v set li gi tr cho value2 .

        ***CCH 2: DNG ViewChild: xem bn di
    */

   //$event: thng qua th <app-child>, bin ny nhn value gi qua t output myClick ca comp Child.
   //gi n s kin output bn child comp trong du ngoc () nh: (myClick)
    template: `
        <h3>{{ value }}</h3>
        
        <app-child (myClick)="changeValue($event);" #child></app-child>
        
        <button (click)="child.value2 = child.value2 + 1">Add for Child-#Child</button> 
        
        <button (click)="onAddForViewChild();">Add for Child-ViewChild</button> 
        
    `
})

/*S dng ViewChild : gi lun component child.ts qua bn parent.ts ny:
-add ViewChild vo trong import core.
-import component cn gi qua y bng ViewChild: import { ChildComponent } from './child.component'; 

-Xung di, vo trong class ParentComponent, dng @ViewChild  gi comp cn gi qua y (comp child.ts) 
v to 1 i tng bi comp cn gi qua . 
+Dng i tng   thao tc. i tng c to t comp viewchild cn gi qua  c xem nh l comp , n c th thao tc set li mi thuc tnh ca comp . 

-ko ln ch template thy button Add for Child-ViewChild, s s dng viewchild ny.
*/


export class ParentComponent implements OnInit {
    
    value = 0;

    constructor() { }

    ngOnInit() { }

    changeValue(isAdd: boolean){
        isAdd ? this.value++ : this.value--; //isAdd=true th ++
    }

    //View child
    @ViewChild(ChildComponent)
    myChild: ChildComponent;

    //gi bin value2 t ChildComponent
    onAddForViewChild(){
        this.myChild.value2++;
    }
}