import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'roundNum' })

/***To ra 1 pipe: -vd: to 1 pipe lm trn ln.
-To 1 file round.pipe.ts trng: file ny cha pipe lm trn cn to.
-import Pipe v PipeTransform t core.
-t tn pipe,  y t roundNum, gi trong html vi tn pipe ny l roundNum.
-implement method transform v chnh sa kdl a vo, kdl a ra ca n.
(a vo 1.9, sau khi lm trn a ra 2.0: nn u l number)

-Cu hnh file pipe ny vo trong app.modules.ts: import ng dn + gi trong hm declarations, 
ging nh cu hnh 1 component bnh thng.


-CH : nu dng lnh  to 1 folder cha cc file name.pipe.ts ring th dng nh comp thi:
+to comp: ng g c compname
+to pipe: ng g p my-new-pipe
(xem hnh pipe-cc lnh trong hnh trong image note. Hoc ti: https://www.npmjs.com/package/@angular/cli )

*/

export class RoundPipe implements PipeTransform {
    transform(value: number, isUpper: boolean, addTo: number): number {  
        // console.log(typeof addTo); // xem addTo l kiu dl g

        
        // bn trong (value): a vo kiu number;
        // bn ngoi ngoc () c :number: tr ra kt qu sau khi pipe cng kiu number.
        // thm tham s  isUpper  nhn tham s truyn vo t bn ngoi.
        //  addTo cng l 1 tham s nhn truyn vo t html.

        // if(isUpper){
        //     return Math.ceil(value + addTo); //nu isUpper=true th lm trn ln
        // }
        // return Math.floor(value + addTo); //nu isUpper=false th lm trn xung

        //Dng ton t 3 ngi
        //addTo n gin ch l 1 tham s truyn vo t html: vd addTo=10, value=1.5 th s lm trn cho (11.5)
        return isUpper ? Math.ceil(value + addTo) : Math.floor(value + addTo);

        // return Math.round(value); //hm lm trn theo ton hc thng thng.
    }
}
