import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
//import 'rxjs/add/operator/toPromise'; //trong angular 6 th ko cn dng ny cng c th toPromise vi get http.



@Component({
  selector: 'app-ip',
  templateUrl: './ip.component.html',
  styleUrls: ['./ip.component.css']
})


/*Bi 35: Khi nim service v http service
Data service: ly d liu t server thng qua ci API no .
-C th vit Api trong comp  thao tc ly service. Nhng  cho component tp trung cho vic iu khin cc view, ta vit cc service ring ra.

Bi ny ta lm chung service trong comp


**CC BC
-Ti app.module.ts: 
+cng ging nh  dng two-way binding [(ngModel)],  service cng phi import dng ny v gi ra di phn imports, nu  import ri th thi: []
import { FormsModule } from '@angular/forms';

+import th vin HttpModule  ly service t server: import { HttpModule } from '@angular/http';
v gi n ra di phn imports: []
=>Lc ny trong ts ta mi c th s dng HttpModule

-Trong comp ny ip.comp.ts: 
+import { Http }  trn cng.
+import toPromise: import 'rxjs/add/operator/toPromise'; 
V khi get dl v l dng Observable,  chuyn dl sang dng Promise th phi import toPromise.
(Trong Angular 6 th  tch hp toPromise, nn ko cn import dng  na.)

+Vit phn ly service trong constructor. ( nn vit trong hm ngOnInit(), cn constructor ch khai bo service thi, qua comp ip2 s lm nh vy)

*/ 

export class IpComponent implements OnInit {

  //kb bin  to nhn Object http tr v
  ip: String='';

  /*Khi to http:
  c php kb bin: (bin: kiu dl) =>bin http c kiu dl Http. Ch cn khai bo bin, Http n s t ng new ngm.
  */
  constructor(private http: Http) { 
     this.http.get('http://ip.jsontest.com/') //ly dl t server ny v, tr dl v kiu Observable
     .toPromise() // chuyn dl sang dng Promise.  c toPromise() th phi import  trn cng (Vi Angular6 th ko cn import na).
     .then(res => res.json()) //chuyn sang kiu json
    //  .then(resJson => console.log(resJson)) //log dl  ra console, resJson ch l tn t sao cng c.
    //  .catch(err => console.log(err)); //nu c li th phng li ra console.
     //Chy v F12 vo console xem kt qu, n s log ra object vi thuc tnh ip cha ip.

     .then(resJson => this.ip = resJson.ip) //gn ip ly t service cho bin ip  trn.
     .catch(err => console.log(err)); //nu c li th phng li ra console.

    // y then: nhn gi tr tr v, v catch phng ngoi l.

    }

  ngOnInit() {
  }

}
