import { Injectable } from '@angular/core';

//service dng get, post, put, delete. Dng HttpClient mi ch ko dng Http(phin bn c)
import { HttpClient, HttpErrorResponse } from '@angular/common/http';

//kt qu get v l kiu Observable nn phi import n
import { Observable } from 'rxjs';

//get api tr v kiu Observable ca model RestTodo. Do  model RestTodo phi c vit vi cc thuc tnh === thuc tnh ca object tr v.
import { RestTodo } from '../folder-models/rest-todo.class';


@Injectable({
  providedIn: 'root'
})
export class RestTodoService {
  //Xem cch to api gi lp trn mockapi.io ti: https://www.youtube.com/watch?v=puaaPncTAoQ&list=PLJ5qtRQovuEOoKffoCBzTfvzMTTORnoyp
  //(trn mockapi.io ng nhp vi tientm96@gmail.com)
  //api ny cha mng object, mi obj gm c: {id: number(t ng tng, ko cn quan tm), title: string, completed: boolean}.
  private API = 'http://5b5ead8e8e9f160014b88d16.mockapi.io/products';

  constructor(private httpClient: HttpClient) { }

  //get api tr v kiu Observable ca model RestTodo. Do  model RestTodo phi c vit vi cc thuc tnh === thuc tnh ca object tr v.
  getAllTodos() : Observable<RestTodo[]> {
    return this.httpClient.get<RestTodo[]>(this.API);
  }


  //to hm thng bo li
  handleError(err){
    //nu tha k (err.error instanceof Error): nu li nm trong danh sch li Error, th  l li t client.
    if(err.error instanceof Error){
      console.log(`Client-side error: ${err.error.message}`);//c php ES6
    }
    //nu ngc li th l li ca server, x l nh sau
    //vd: li truyn sai api th s in ra li: 404(status) - notFound(error)
    else{
      console.log(`Server-side error: ${err.status} - ${err.error}`);
    }
  }
  

  //POST:
  //Vit phng thc http, gi request post dl ln server  insert d liu.
  //Xem li service: sign-in3.service.ts, cu trc post l: post(url, body, header?)

  //a request post ln server, sau  nhn response tr v, nn s c return.
  //Service s nhn tr v l 1 object RestTodo, sau  truyn qua comp bng cch comp gi service ny.

  //Sau khi POST ln server th dl c a vo api v lu cng lun trong , vo mockapi.io  check dl.
  addRestTodo(resttodo: RestTodo) : Observable<RestTodo> {
    //gi phng thc  post ln, v return v gi tr c response v, l tr v object va post ln .
    return this.httpClient.post<RestTodo>(this.API, resttodo); //this.API l url nhn dl post ln, resttodo l dl cn post ln.
  }



  //PUT: Update
  //Tng t nh post, put cng tr v object sau khi update cho object .
  updateRestTodo(resttodo: RestTodo) : Observable<RestTodo> {
    return this.httpClient.put<RestTodo>(`${this.API}/${resttodo.id}` , resttodo); 
    
    //this.API/id l url nhn dl put ln, resttodo l dl cn put ln.
  
    //Ch : khi put  update th url phi km theo 1 thuc tnh mang tnh duy nht ca obj l id chng hn
    //   phn bit object cn update, nn s c /${resttodo.id}  ly thm id vo url. 
  }

  //DELETE
  //Tng t nh put,http delete cng tr v object sau khi delete object  trn server.
  deleteRestTodo(id: number) : Observable<RestTodo> {
    return this.httpClient.delete<RestTodo>(`${this.API}/${id}`); 
    
    //this.API/id l url nhn dl cn delete, xa theo id(hay bt k thuc tnh duy nht no ty mnh chn,
    // y ch c mi id l thuc tnh duy nht).
  }


}
