import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// cc comp c s dng module routing ny: khi routing s s dng c ngIF, ngFor: th  module routing ny phi import th vin ny. 
//V gi xung phn imports[] trong @NgModule({}) bn di.
import { CommonModule } from '@angular/common';

import { RouterHomeComponent } from './router-home.component';


//canDeactive service
import { AccessGuard } from '../../folder-services/guards/access.guard';




const routesConfig: Routes = [
    { 
        path: 'index', 
        component: RouterHomeComponent,

        /*gi service canDeactivate: [AccessGuard]: cho lin kt path no mun gi li, ko cho thot ra khi path ,
            lin k path trong router-home.module.ts ny.
        Nu auth.guard.ts tr v true th c th thot ra, cn false th ko th thot ra khi path .
 
        Xem trong access.guard.ts. Nh import service v gi xung providers.*/
        canDeactivate: [AccessGuard]
    }
];


@NgModule({
    imports: [
        CommonModule,
        // RouterModule.forRoot(routesConfig)
        RouterModule.forChild(routesConfig) //v y l module con ch vi 1 comp, nn gi forChild() ch ko phi forRoot nh module gp nhiu comp.
    ],

    //declarations[]  gi class ContactsComponent t comp  import vo (ging nh bn app.module.ts cng import xong th gi xung declarations)
    declarations: [RouterHomeComponent],

    //providers: ni import service
    providers: [
        AccessGuard
    ]
    
})

export class HomeModule {}

