import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";

@Component({
  selector: "app-child2",
  templateUrl: "./child2.component.html",
  styleUrls: ["./child2.component.css"]
})
export class Child2Component implements OnInit {
  /*
  -C 2 cch t tn bin input: Vi keyname l tn input c gi  comp cha
  (nu comp cha mun gi ti input ny th n s gi keyname v gn gi tr vo keyname)

  +@Input() tenbienInput: kdl;  Cch ny th tenbienInput === keyname
  +@Input('keyname') tenbienInput : kdl;  Cch ny th tenbienInput c th !==/=== keyname
  */

  //Dng cch t tn bin input !== keyname
  // @Input('married') isMarried : boolean = false;

  //Dng cch t tn bin input === keyname
  @Input() married: boolean = true;

  //C th gn gi tr default hoc ko. Gi tr default s c dng nu nh input ko c truyn gi tr vo.
  @Input() age: number = 20;

  @Input() name: string = "Tin bnh thng";

  //Output: bin ny l dl output gi t child2 comp qua parent2 comp.
  public txtFullName: string;
  public txtphoneNumber: number;


  constructor() {}

  ngOnInit() {}



  //to ra keyname ng thi l tn bin s kin output, km theo <kiu dl tham s cn truyn qua parent2>, nu ko bit kdl g th  <any>.
  // @Output() onHandleFullName = new EventEmitter<any>();
  @Output() onHandleFullName = new EventEmitter<string>();
  @Output() onHandlePhoneNumber = new EventEmitter<number>();


  //cch khc: truyn keyname !==/=== tenbienOutput (bin th sd cho child comp, cn keyname l  gi bn parent comp)
  // @Output('onHandleFullName') myClick = new EventEmitter<boolean>();

  //mi ln gi n sk ny th s gi dl txtFullName qua comp cha, thng qua bin output onHandleFullName.
  onSubmitForm() {
    this.onHandleFullName.emit(this.txtFullName);
    this.onHandlePhoneNumber.emit(this.txtphoneNumber);
  }

}
