import { Injectable } from '@angular/core';
import { Product } from '../folder-models/product';


@Injectable(
  // {providedIn: 'root'}
)

export class ProductService {

  public products : Product[] = [
    {
      id : 1,
      name : 'Iphone 7',
      price : 1000,
      status : true
    },
    {
      id : 2,
      name : 'Iphone 10',
      price : 2000,
      status : false
    },
    {
      id : 3,
      name : 'Xiaomi',
      price : 3000,
      status : false
    },
    {
      id : 4,
      name : 'Note 7',
      price : 4000,
      status : true
    }
  ];

  constructor() { }

  // getAllProducts(){
  //   return this.products;
  // }

  //tham s name?: du ? l tham s ny c hoc ko c cng c, truyn vo 1 hoc 2, hoc 0 tham s u c. 
  //Hm ny lm lun nhim v ca hm getAllProducts c ri
  getAllProducts(name?: string, price?: number){
    
    let result : Product[] = this.products;
    
    //nu c ts name, price th lc  cho ra mng theo cc tham s . S dng filter  lc, trc khi lc cho v thng ht hoc hoa ht. 
    //hm A.indexOf(B) kim tra xem c chui B trong chui A hay ko, tr v v tr, nu ko c tr -1. 
    if(name){
      result = this.products.filter(x => {
        return x.name.toLowerCase().indexOf(name.toLowerCase())  != -1;
      });
    }

    if(price){
      result = this.products.filter(x => {
        return x.price === (+price); //p v kiu int ri mi so snh === c, cn ko p th so snh ==
      });
    }

    return result;
  }


  getProductByID(id : number){
    let result = null;
    for(let i = 0; i < this.products.length; i++){
      if(this.products[i].id === id){
        result = this.products[i];
        break;
      }
    }
    return result;
  }


  //Update mng
  updateProduct(product : Product){
    const index = this.products.findIndex(e => e.id === product.id);
    this.products.splice(index, 1, product); 

    //SPLICE: ti v tr index, xa 1 phn t, add phn t product vo li v tr index .

    //Xem r hn v splice: va xa va add: https://www.tutorialspoint.com/typescript/typescript_array_splice.htm
  }

  deleteProduct(id: number){
    const index = this.products.findIndex(e => e.id === id);
    this.products.splice(index, 1); //xa t phn th th index, xa 1 phn t
  }

}
