
import { Component, OnInit, OnDestroy } from '@angular/core';

//import  y  lt ra sd.  import v gi ra providers[] trong app.module.ts (dng c cho all comp), nn  y ko cn providers[]
import { RestTodoService } from '../../folder-services/rest-todo.service';

//gi model
import { RestTodo } from '../../folder-models/rest-todo.class';

//Tng t vic ly params trn link, dng Subscription  lng nghe. 
//Th vic ly api cng s dng i tng Subscription  lng nghe, nn phi to ra v hy n i.
import { Subscription } from 'rxjs';




@Component({
  selector: 'app-rest-todo',
  templateUrl: './rest-todo.component.html',
  styleUrls: ['./rest-todo.component.css']
})
export class RestTodoComponent implements OnInit, OnDestroy {

  public resttodos : RestTodo[] = []; //array

  //nu to object th bn html sd [(ngModel)]="resttodo.title", 
  //m  y nu  public resttodo : RestTodo = null; th s b li, v null th ban u u c g  ngModel ly ra u.
  //Vy nn  sd ngModel vi object th object  phi c khi to new trc.
  //Khai bo  y vo vo ngOnInit new ra: resttodo = new RestTodo(id, title, completed);

  //hoc ko cn new, dng ngIf bn html, nu =null th ng cho hin ln.
  public resttodo : RestTodo; // Kb 1 object

  //hoc cch khc l sd cc bin thnh phn,  ngModel cho n.
  //cc bin mun ngModel th phi  public mi c. Thng th trong angular nn  cc kb di dng public.
  public title : string; 
  public completed : boolean = false;


  //dng i tng Subscription  lng nghe dl ly t api v, nn phi hy n i. 
  private subscription : Subscription;



  constructor(private restTodoService : RestTodoService) { }

  ngOnInit() {
    this.loadData();
  }

  //hy i tng subscription khi comp kt thc.
  ngOnDestroy(){
    if(this.subscription){
      this.subscription.unsubscribe();
    }
  }
  
  loadData(){
    this.subscription = this.restTodoService.getAllTodos().subscribe(data => {
      this.resttodos = data;
    }, error => {
        // console.log(error);
      this.restTodoService.handleError(error); //gi service x l li nu c.
    });

  }


  onAddRestTodo(){
    console.log(`${this.title} - ${this.completed}`); //ktra cc dl nhp vo t ngModel c c ko

    // HM NY MUN DEBUGGER TH T DNG debugger  Y. CHY LN F12>SOURCE, A CHUT VO GI TR CN XEM, F8  THOT DEBUGGER.  
    // debugger


    //trong constructor ca class RestTodo ch c title v completed, id ko v  set trn api l t ng tng, nn ko cn.
    //Ta ch cn truyn ln nhng thnh phn khc, id s c mockapi.io t ng tng.
    let restTodo = new RestTodo(this.title, this.completed);

    this.subscription = this.restTodoService.addRestTodo(restTodo).subscribe(data => {
      //sau khi Post dl ln th nhn gi tr c respone v l data l 1 object va post ln.
      //ta cn ly data ny v  cp nht li mng hin th qua html.
    
      //cp nht li mng.
      this.resttodos.push(data); //lc ny push vo mng resttodos,  n hin th qua html cho ng cp nht.
    
      console.log(data.completed);
    }, error => {
        // console.log(error);
      this.restTodoService.handleError(error); //gi service x l li nu c.
    });
  }

  
  //-----------------------PUT, DELETE---------------------------------------------

  //Ch c khi bm vo edit th bin resttodo mi c gn gi tr, mi ht null, nh vy form edit trong html mi hin.
  onEditTodo(resttodo: RestTodo){
    this.resttodo = resttodo;
  }

  //sk khi nhn nt save update trong form edit.
  onUpdateRestTodo(){
    this.subscription = this.restTodoService.updateRestTodo(this.resttodo).subscribe(data => {
      //sau khi PUT dl ln th nhn gi tr c respone v l data l 1 object va c cp nht.
      //ta cn ly data ny v  cp nht li mng hin th qua html.

      //cp nht li mng, gn li thnh phn  update vo mng c.
      let index = this.getIndex(data.id);
      this.resttodos[index] = data;

    }, error => {
      this.restTodoService.handleError(error); 
    });

    // sau khi nhn save  update, th cho n form edit i, bng cch gn null li cho bin resttodo.
    this.resttodo = null;
  }

  //hm ly v tr index ca object trong mng, ly theo thuc tnh id ca object .
  getIndex(id: number) : number {
    let result = 0;

    this.resttodos.forEach((item, index) => {
      if(item.id ===  id){
        result = index;
      }
    });
    return result;
  }

  //kch vo nt Delete trn row of table, th n s gi v id, da theo id  m xa.
  onDeleteTodo(id: number){
    this.subscription = this.restTodoService.deleteRestTodo(id).subscribe(data => {
      //sau khi delete th nhn gi tr c respone v l data chnh l object va b delete.
      //ta cn ly data ny v  cp nht li mng hin th qua html.

      //cp nht li mng.
      let index = this.getIndex(data.id);
      this.resttodos.splice(index, 1); //xa ti v tr index, xa 1 phn t

    }, error => {
      this.restTodoService.handleError(error); 
    });
  }


}
