import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-user-form',
  templateUrl: './user-form.component.html',
  styleUrls: ['./user-form.component.css']
})
export class UserFormComponent implements OnInit {

  name = ''; //khai bo bin name rng
  name2 = ''; //two-way binding, dng cho cch 4 s dng ngModel.
  name3 =''; isHighLight = true;
  evenStyle= { color: 'red', fontSize: '40px'}; //chn (s lng ch nhp vo l chn)
  oddStyle= { color: 'black', fontSize: '20px'}; //l


  current = { circle: !this.isHighLight, square: this.isHighLight};
  
  constructor() { }

  ngOnInit() {
  }

  showEvent(event){
    // Nu ch xut ra $event th console.log($event) ra s c rt nhiu thuc tnh, kch vo mc target s c thuc tnh value l gi tr ni cui cng nhn c, 
    //  nn s gi n value  ly value gn cho name. 
    // Khi  sau mi ln nhp 1 k t th name li c gn li bi 1 value di hn. 
    // console.log(event);
    this.name = event.target.value;
  }

  //hm dng cho template variable, nhn tham s truyn vo t temp #txtUserName
  username='';
  onGetUser(username) : void{
    //  console.log(username);
    this.username = username; 
	}

  /*cch 4.2: two-way binding bng cch dng thuc tnh directive: https://angular.io/guide/architecture-components#data-binding
  Trong link c dng: <input [(ngModel)]="hero.name">  dng ny  dng trong file html.
  
   file ts ch cn khai bo name2='', cn vic gn cc gi tr thay i cho name2  c ngModel lo.
  Ch cn qua html gn name2 vi ngModel l c.
  */

}
