import { Component } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  nameComp = 'To Nguyn';
  ageComp = 18;
  
  total : number= 0;

  public a : number;
  public b : number;

  isDestroy = false;

  constructor(private router: Router) { }



  onHandleDestroy(){
    this.isDestroy = !this.isDestroy;
  }

  //Nu dng ([ngModel])
  // amount(){
  //   this.total = this.a + this.b;

  //   console.log(typeof this.a, typeof this.b);
  // }
  
 
  //Nu dng template reference variable #a, #b
  /*Ch : v l bin template nn n lun nhn vo dng text, d ta c  input u vo l number i chng na.
  Vy nn template ref var lun tr v dng text  mi trng hp. 

  Mun chuyn sang s th ta phi p kiu: hoc l Number(a) hoc l parseInt(a)*/ 
  amount(a , b){
    console.log(typeof a, typeof b);

    this.total = Number(a) + parseInt(b);
  }


  //bm btn logout th n s hy bin ton cc localStorage, nu bin ny difined vi key l user.
  logout(){
    if(localStorage.getItem('user')){
      localStorage.removeItem('user');
      // localStorage.clear();


      //nu ko remove th n s lu li trng thi ca localStorage sau mi ln F5 u v t login vo c. 
      //nn phi remove, hoc xa cokie ca trnh duyt.
    }

    //bm btn logout th s chuyn v ng dn mc nh l /index (home)
    //dng navigate  nh dng li ng link.
    this.router.navigate(['']);
  }
}
