import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';



// dng Template-driven forms; two-way binding(l ngModel); service: phi import FormsModule v gi ra di phn imports: []
// s dng Reactive Form phi import ReactiveFormsModule v cng gi ra di phn import[].
//V TA TCH RING MODULE, NN  COMP NY MUN SD CI G TH PHI IMPORT RING, CH KO PH THUC VO APP.MODULE.TS
import { FormsModule } from '@angular/forms';





// cc comp c s dng module routing ny: khi routing s s dng c ngIF, ngFor: th  module routing ny phi import th vin ny. 
//V gi xung phn imports[] trong @NgModule({}) bn di.
import { CommonModule } from '@angular/common';


import { RouterProductsComponent } from './router-products.component';
import { RouterProductListComponent } from '../router-product-list/router-product-list.component';
import { RouterProductEditComponent } from '../router-product-edit/router-product-edit.component';
import { RouterLoginComponent } from '../router-login/router-login.component';




//service
//Guard l 1 service, lm canactive cho login.
import { AuthGuard } from '../../folder-services/guards/auth.guard';




const routesConfig: Routes = [
    { 
        path: 'products', 

        /*-nu c dng ny th c ngha l i n RouterProductsComponent, bn children mun c gi ra th phi nm trong <outlet> 
        ca RouterProductsComponent, phi thit lp 1 th <outlet> bn trong RouterProductsComponent .
        -nu ko c dng ny th s i trc tip ti children mi khi link = products/
        - y ta  nguyn dng ny vi  mun router 1 comp bn trong 1 comp, cc ni dung ca comp con s  vo <router-outlet> ca comp cha.
        */
        component: RouterProductsComponent, 

        /*gi service canActivate: [AuthGuard]: cho lin kt path no mun bo v, lin k path trong router-products.module.ts.
        nu auth.guard.ts tr v true th n cho vo, cn false th n ko cho vo path .
        Xem trong auth.guard.ts. Nh import service v gi xung providers.*/
        canActivate: [AuthGuard],

        
        //Thay v /products s i thng ti RouterProductsComponent, th s i vo child{} v s ni cc ui child vo, vd: /products/list
        children: [
            //cu hnh ng dn khi kch vo products th s chy ng dn mc nh l: /products/list
            {
                path: '',
                redirectTo: '/products/list',
                pathMatch: 'full'
            },
            {
                path: 'list', //ng dn products/list s n com RouterProductListComponent
                component: RouterProductListComponent
            },
            {
                path: 'edit/:id', 
                component: RouterProductEditComponent
            }
        ]
    },
    {
        path: 'login',
        component: RouterLoginComponent
    }
];



@NgModule({
    imports: [
        FormsModule,
        CommonModule,
        // RouterModule.forRoot(routesConfig)
        RouterModule.forChild(routesConfig) //v y l module con ch vi 1 comp, nn gi forChild() ch ko phi forRoot nh module gp nhiu comp.
    ],

    //declarations[]  gi class ContactsComponent t comp  import vo (ging nh bn app.module.ts cng import xong th gi xung declarations)
    declarations: [
        RouterProductsComponent, 
        RouterProductListComponent, 
        RouterProductEditComponent, 
        RouterLoginComponent
    ],

    //providers: ni import service
    providers: [
        AuthGuard
    ]
})

export class ProductsModule {}

