/*Y L MODULE: lm nhim v gp tt c module n l li, ri add vo module chnh l app.module.ts.

CH : module chnh app.module.ts ch add 1 file root duy nht, nn phi gp ht tt c module con vo trong 1 file app-routing.module.ts, ri mi add vo app.module.ts.*/

/*
-Tch nhng code  routing bn app.module.ts ra thnh 1 module khc app-routing.module.ts.
(ri s gi app-routing.module.ts vo app.module.ts:  router. Nh v s lm gn app.module.ts hn)

GII THCH CHO app-routing.module.ts:
-----------------
**Ti routing module c tch ra. To ra file mi: app-routing.module.ts (ngang hng vi app.module.ts)
-import NgModule t core
-import Routes, RouterModule t router
-import 3 class ca 3 component: Contacts,ContactDetail,PageNotFoundComponent (nhng comp no c lin quan n vic routing th tch ra import trong ny)

-To ra routesConfig, copy t app.module.ts qua, bn  c gii thch r rng ri

-To decurator function tn l: @NgModule({})  cha:
 +import[] nhng ci cn thit cho router nh trong app.module.ts 
 +declarations[]  gi cc class (3 class ContactsComponent,ContactDetailComponent,PageNotFoundComponent) comp  import vo (ging nh bn app.module.ts cng import xong th gi xung declarations).

 +(s bo li v @NgModule({}) cn phi to thm export class tip theo sau y,  n lin kt)


 +expors (exports c s): [RouterModule] : Ti v trong app.component.html c dng router-outlet nn  y ta phi exports RouterModule ra.

-To export class AppRoutingModule{}
----------------------------------------------------------------------------------------------------

GII THCH CHO app.module.ts
----------
**Ti app.module.ts: XA HOC Cmt LI CC khai bo  tch ra bn ny. IMPORT FILE module TCH VO LI app.module.ts

-import module  tch ra vo app.module.ts: import { AppRoutingModule } from './app-routing.module';
ko xung phn imports[] gi AppRoutingModule vo.
 
-V  import AppRoutingModule vo ri, nn s xa nhng ci d tha  vit li bn AppRoutingModule.

+XA import { Routes, RouterModule } from '@angular/router';

+XA bin const routesConfig v cc khai bo ca n.
ko xung phn imports[], xa dng gi RouterModule.forRoot(routesConfig) trong imports[] .

+XA cc import ContactsComponent, ContactDetailComponent, PageNotFoundComponent; 
v xa cc ln gi ca cc comp ny trong declarations[]. 
(V  chuyn qua app-routing.module.ts ri, nn  y ko cn na)

*/



import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// cc comp s dng module routing ny: khi routing s s dng c ngIF, ngFor: th  module routing ny phi import th vin ny. 
//V gi xung phn imports[] trong @NgModule({}) bn di.
import { CommonModule } from '@angular/common';





/*TCH RA MODULE CHILD CHO 1 COMP CONTACTS: V  TCH MODULE RING CHO COMP CONTACTS, NN S CMT/XA NHNG TH  TCH; V IMPORT GI MODULE CHILD  VO:

-import ContactsModule ca contacts.module.ts vo y; ko xung phn import trong @NgModule  thm ContactsModule vo:
    +nu l Angular4 th phi thm  u imports: [ContactsModule, RouterModule.forRoot(routesConfig), CommonModule ]
    +nu l Angular6 th thm  u cng c,  y thm  cui imports: [RouterModule.forRoot(routesConfig), CommonModule, ContactsModule ]

-XA/cmt: import { ContactsComponent } v ln gi n trong declarations[] bn di

    
-XA/cmt: { path: 'contacts', component: ContactsComponent }, v  move qua bn contacts.module.ts*/

// import { ContactsComponent } from './contacts/contacts.component';
import { ContactsModule } from '../folder-components/contacts/contacts.module';

import { HomeModule } from '../folder-components/router-home/router-home.module';
import { Contact2Module } from '../folder-components/router-contact2/router-contact2.module';
import { AboutModule } from '../folder-components/router-about/router-about.module';
import { ProductsModule } from '../folder-components/router-products/router-products.module';
import { ProductDetailModule } from '../folder-components/router-product-detail/router-product-detail.module';






//2 comp ny chng ta ko tch ra module ring, m  gp chung trong 1 module ny, nn gi vo  x l path ti y lun.
import { ContactDetailComponent } from '../folder-components/contact-detail/contact-detail.component';
import { PageNotFoundComponent } from '../folder-components/page-not-found/page-not-found.component';






const routesConfig: Routes = [
    
    /*-Kch vo ng dn contacts, th s lm vic trn component ContactsComponent, v chuyn mn hnh n ContactsComponent.

    -ng dn path s c truyn vo t routerLink="/detail/{{ contact.id }}" trong html, khi kch vo s cho ra link /detail/1 /detail/2 /detail/3 ...
    Vy khi link l /detail/1 th path:'detail/:id' s nhn ng dn ny v ng vi format ca n, ri chuyn n ContactDetailComponent.

    { path: 'detail/:id/:name/:phoneNumber', component: ContactDetailComponent } thng qua ng format vi routerLink bn html  ly dl t bn .
    

    -***CH :  Y S LY id, name, phoneNumber t contacts html  truyn qua ContactDetailComponent: XEM gii thch QUY TRNH TRUYN DL ti file contacts.component.html.
    */

    // { path: 'contacts', component: ContactsComponent },  //XA V  TCH RA MODULE CHILD RING RI: contacts.module.ts

    // { path: 'detail', component: ContactDetailComponent }, //B DNG NY TH KHI KCH VO nt link Detail bn app.component.html n s tr v comp page-not-found.
    
    // xem ch   trn. Chuyn mn hnh qua comp ContactDetailComponent, ng thi truyn km cc dl id, name, phoneNumber qua comp .
    { path: 'detail/:id/:name/:phoneNumber', component: ContactDetailComponent },



    // chnh 1 trang lm home, ngha l khi vo http://localhost:4200 th n t ng chuyn n /index: http://localhost:4200/index ;
    //  ging kiu thit lp trang mc nh trong .net MVC,  y thit lp trang mc nh l index.
    { path: '', redirectTo: '/index', pathMatch: 'full' },


    
    //Nu + vo link 1 a ch li th n s chy n trang ca comp page-not-found (t to comp ny): vd: http://localhost:4200/sajsaksjla l 1 a ch li.
    // y ta thit lp dn n trang LI khi chy a ch li ti y.
    { path: '**', component: PageNotFoundComponent }  //du **  l ko vo c tt c comp no ri th mi chy n comp PageNotFoundComponent

];

//To @NgModule  gi cc module v comp  import  trn.  
// vit tch Contact ra 1 module ring, nn s import  trn v gi vo import di y l  ri.
@NgModule({
    //ContactsModule: nu l Angular 4 th phi thm  u import[ContactsModule,HomeModule,,,]
    imports: [
        RouterModule.forRoot(routesConfig), 
        CommonModule,
        ContactsModule, 
        HomeModule, 
        Contact2Module, 
        AboutModule, 
        ProductsModule,
        ProductDetailModule
    ], 
    declarations: [
        // ContactsComponent,

        //2 comp ny chng ta ko tch ra module ring, m  gp chung trong 1 module ny, nn gi vo  x l path ti y lun.
        ContactDetailComponent,
        PageNotFoundComponent
    ],

    // +expors (exports c s): [RouterModule] : Ti v trong app.component.html c dng <router-outlet/> nn  y ta phi exports RouterModule ra.
    exports: [RouterModule]
})

export class AppRoutingModule { }
