import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';


@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.css']
})

/*
**Reactive Forms: https://angular.io/guide/reactive-forms
-bn app.module.ts: import ReactiveFormsModule t @angular/core, ri gi ra di phn import[].
-trong comp: import FormGroup, FormControl
-trong class chnh ca comp: xem trong y

-Qua html xem cch gi form ntn. Bn html s truyn dl nhp vo qua cho object formSignUp v cc tham s ca formSignUp.
 y ch cn log ra formSignUp.value l c.


*/ 
export class SignUpComponent implements OnInit {

  formSignUp: FormGroup; //khai bo thuc tnh formSignUp thuc class FormGroup(). thuc tnh ny nhn cc gi tr tr v t gn [formGroup]="formSignUp" ca html.
  

  constructor() { 

    //khi to formSignUp trong constructor hoc onInit(), vi cc tham s bn trong l cc object FormControl()
    this.formSignUp = new FormGroup({
      email: new FormControl('tientm96@gmail.com'), //a cc gi tr mc nh vo ('default')
      password: new FormControl(),

      //to cc FormControl checkbox  nhn cc gi tr checkbox gi t html v.
      //to FormGroup  nhn nguyn 1 object mang gp cc gi tr ca checkbox gi v.
      subject: new FormGroup({
        nodeJS: new FormControl(false), //a vo cc gi tr mc nh, false l cha check, true l default= check
        angular: new FormControl(true),
        reactJS: new FormControl(false)
      }),


    });
  }

  ngOnInit() {
  }

  onSubmit(){
    console.log(this.formSignUp.value);
  }

}
