/*
-To ra nhng ci ngModule nh hn,  qun l code tt hn trong nhng ci module ring nh app-routing.module.ts
=>Vit module ring cho mi comp c s dng routing.

THC HIN
-trong folder contacts, to contacts.module.ts 
-trong contacts.module.ts: (tng t nh app-routing.module.ts: xem c th trong file ).

GII THCH CHO contacts.module.ts:
-----------------
**Ti routing module c tch ra. To ra file mi: app-routing.module.ts (ngang hng vi app.module.ts)
-import
+import NgModule t core
+import Routes, RouterModule t router
+import CommonModule t common:  comp contacts routing vi module ny, khi render ra html s s dng c ngIF, ngFor
+import class component ca n: Contacts ( y lm module ring cho comp contacts).

-To ra routesConfig (copy t app.module.ts qua, bn  c gii thch r rng ri): bn trong ch cn 1 path  iu hng n ContactsComponent thi.

-To decurator function tn l: @NgModule({})  cha:
 +import[] nhng ci cn thit cho router nh trong app.module.ts
 +declarations[]  gi class ContactsComponent t comp  import vo (ging nh bn app.module.ts cng import xong th gi xung declarations)

 +(s bo li v @NgModule({}) cn phi to thm export class tip theo sau y,  n lin kt)

 +CI NY TRONG MODULE CON, NN KO CN: expors(exports c s): [RouterModule] : Ti v trong app.component.html c dng router-outlet nn  y ta phi exports RouterModule ra.

-To export class ContactsModule{}
----------------------------------------------------------------------------------------------------

GII THCH CHO app-routing.module.ts
----------
**Ti app-routing.module.ts: XA HOC Cmt LI CC khai bo  tch ra bn ny. IMPORT FILE module TCH (contacts.module.ts) VO LI app-routing.module.ts

-import ContactsModule ca contacts.module.ts vo y; ko xung phn import trong @NgModule  thm ContactsModule vo:
    +nu l Angular4 th phi thm  u imports: [ContactsModule, RouterModule.forRoot(routesConfig), CommonModule ]
    +nu l Angular6 th thm  u cng c,  y thm  cui imports: [RouterModule.forRoot(routesConfig), CommonModule, ContactsModule ]

-XA/cmt: import ContactsComponent v ln b gi n trong declarations[] bn di

-XA/cmt: { path: 'contacts', component: ContactsComponent }, v  move qua bn contacts.module.ts

*/


import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// cc comp c s dng module routing ny: khi routing s s dng c ngIF, ngFor: th  module routing ny phi import th vin ny. 
//V gi xung phn imports[] trong @NgModule({}) bn di.
import { CommonModule } from '@angular/common';

import { ContactsComponent } from './contacts.component';

const routesConfig: Routes = [
    { path: 'contacts', component: ContactsComponent }
];


@NgModule({
    imports: [
        CommonModule,
        // RouterModule.forRoot(routesConfig)
        RouterModule.forChild(routesConfig) //v y l module con ch vi 1 comp, nn gi forChild() ch ko phi forRoot nh module gp nhiu comp.
    ],

    //declarations[]  gi class ContactsComponent t comp  import vo (ging nh bn app.module.ts cng import xong th gi xung declarations)
    declarations: [ContactsComponent] 
})

export class ContactsModule {}

