
//PIPE: sp xp array number

import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sortNumber'
})
export class SortNumberPipe implements PipeTransform {

  transform(arrNumber: number[], sortValue: number): any {
    // console.log(typeof args); // xem args l kiu dl g
    
    /*CH :
    Ta sp xp trc tip trn mng, nhng gi tr tr v li ko gn vo mng, m phi to ra 1 bin
    var result  lu gi tr sau khi sp xp.

    Sx xong tr v mng mi thng qua kt qu var result: return [...result];
    C php [...result] l to 1 array[] mi, ri copy result vo array mi .
    */ 

    /*dn hm sort() c sn  sx, c ch ca hm sort: 
    nu sp xp tng dn th: a > b tr v 1, a < b tr v -1, a = b tr v 0,
    nu sp xp gim dn th: a > b tr v -1, a < b tr v 1, a = b tr v 0.
    Mnh ch cn a k vo nh v, sort() n t sx.
   
    Ta dng param sortValue  a gi tr 1/-1 vo  sx tng dn/gim dn theo  mun.
      nu param sortValue=1 th hm sort() s c c ch sx tng dn, v ngc li.
    */
    var result = arrNumber.sort((a, b) => {
      return a > b ? sortValue : (a < b ? -sortValue : 0);
    });


    /*Cch 2: Ban u sx cng l tng dn, sau gim dn th ch cn o mng li.
    Dng hm  reverse()  o mng.*/ 
   
    // var result = arrNumber.sort((a, b) => {
    //   return a > b ? 1 : (a < b ? -1 : 0);
    // });

    // if(sortValue === -1){
    //   result = result.reverse();
    // }

    //tr v 1 mng mi, copy t mng result, xem gii thch trn phn CH   trn.
    return [...result];
  }

}
