import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';


/*Bi 39: CanActive
-L 1 service kim tra vic cho php ngi dng c vo c router  hay khng. Thng c dng cho admin.
Ni cch khc l chn ko cho ngi dng vo router no .
----------
THC HIN:
-v CanActive cng l 1 service, nn to fodel guards trong folder services.
-Vo cli, xem c php: teminal: "ng g guard auth" (my-new-guard)  to nhanh service canactive vi tn file: auth.guard.ts
-import vo router-products.module.ts, vo ko xung providers thm vo.
-gi canActivate: [AuthGuard]: cho lin kt path no mun bo v, lin k path trong router-products.module.ts.
nu auth.guard.ts tr v true th n cho vo, cn false th n ko cho vo path .

- iu chnh true/false ca auth.guard.ts, thng qua comp router-login th phi dng 1 bin ton cc(c h thng u dng c): 
l bin localStorage(key: value(string)), bin ny s c set,get gi tr theo key, all comp u sd c bin ny.

-bin localStorage s lu li gi tr vi key c set, vy nn mun logout th phi hy bin localStorage ny: 
hy ti btn logout  app.component.ts.
*/


@Component({
  selector: 'app-router-login',
  templateUrl: './router-login.component.html',
  styleUrls: ['./router-login.component.css']
})
export class RouterLoginComponent implements OnInit {

  public error : number = 0;

  constructor(private router: Router) { }

  ngOnInit() {
    // this.checkLogin();
  }

  checkLogin(){
    if(localStorage.getItem('user')){
      this.router.navigate(['']);
    }
  }


  //nhn cc gi tr t bin template ref variable #bin
  onLogin(username : string, password : string){
    //bin object user c kiu key:value
    let user = {
      username: username, //key: value, vi value l  cc params truyn vo. 
      password: password
    };

    // localStorage l bin ton cc ca angular, bin ny dng  kt ni vi auth.guard.ts
    //  bin ny nhn vo gi key: value(string),  y key l user.
    if(username === 'admin' && password === 'admin'){
      localStorage.setItem('user', JSON.stringify(user)); //JSON.stringify: chuyn obj user sang kiu JSON (string).

      //nu ng th set ng dn ti products.
      this.router.navigate(['products']);
    }else{
      //ngc li nu ngi dng cha login th gn -1
      this.error = -1;
    }

  }
}
