import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
// import 'rxjs/add/operator/toPromise'; //trong angular 6 th ko cn dng ny cng c th toPromise vi get http.

//service ly thi tit thng qua api: Xem k v service trong folder: ip2, ip.service.ts


@Injectable()


export class WeatherService {
    //kb bin http t th vin Http, angular s t ng new ngm cho n thnh i tng ca Http. 
    constructor(private http: Http) { } 

    //hm ly nhit 
    getTemp(cityName: string){
        //url ly t api trang https://openweathermap.org/ . Xem trong file NOTE.txt.
        //qua api ny, c th ly c thng tin thi tit ca thnh ph thng qua tn thnh ph truyn vo.
        const url = 'https://api.openweathermap.org/data/2.5/weather?appid=e490064ea5c52b53fb81809ab6a3059f&units=metric&q=' + cityName;

        return this.http.get(url)
        .toPromise() 
        .then(res => res.json())
        .then(resJson => resJson.main.temp) //F12 theo url th cu trc json cha weather l main bc bn ngoi temp 
    }

}