import { Injectable } from '@angular/core';

//class model  Movie
import { Movie } from '../folder-models/movie';


@Injectable(
  // {providedIn: 'root'}
)

export class MovieService {

  //to mng cha object
  public movies : Movie[] = [
    new Movie(1, 'm nh mnh', 'https://www.youtube.com/watch?v=PIEbejHvDcI', 'Tun Hng'),
    new Movie(2, 'Ngi sao bay', 'https://www.youtube.com/watch?v=_jNZN-t2zE8', 'an Trng')
  ];

  constructor() { }

  getAllMovie(){
    return this.movies; 
  }

  addVideo(movie : Movie){
    movie.id = this.getLastID(this.movies); //last id ca mng hin ti
    this.movies.push(movie);
  }

  getLastID(movies){
    /*ton t 3 ngi: 
    -nu movies.length > 0 ngha l true th tr v movies.sort()...: dng sort()  sx mng tng dn theo id, 
    ri tr v id ca pt cui cng + 1  lm id cho phn t tip theo chun b c push vo ny.

    +C php: sort()[index].property : hm sort() sx xong tr v mng  sx l mng A chng hn: nn c php  chnh l A[index].id hay A[index].name l chnh xc.

    -nu movies.length = 0 ngha l false, cha c pt no, nn thm s l thm vo pt u tin nn cho id u tin l 1. 
    
    =>Tm li: sx tng theo id  tm ra id cui cng, ri + 1  thnh id ca phn t cn thm vo.
    */
    let lastID = movies.length > 0 ? 
    movies.sort((a, b) => {
      return a.id > b.id ? 1 : (a.id < b.id ? -1 : 0);
    })[movies.length-1].id + 1 : 1;

    return lastID;
  }


  //Xa phn t theo id: double click vo vo list th s xa
  deleteVideo(id: number){
    const index = this.movies.findIndex(e => e.id === id);
    this.movies.splice(index, 1); //xa t phn th th index, xa 1 phn t
  }

  //Update mng theo id
  updateVideo(movie : Movie){
    const index = this.movies.findIndex(e => e.id === movie.id);
    this.movies.splice(index, 1, movie); 

    //SPLICE: ti v tr index, xa 1 phn t, add phn t movie vo li v tr index .

    //Xem r hn v splice: va xa va add: https://www.tutorialspoint.com/typescript/typescript_array_splice.htm
  }


}

