import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { Router } from '@angular/router';


@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate {

  constructor(private router: Router) { }

  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
      
      //nu bin ton cc localStorage vi key l user m difined th true: cc path sd Guard ny s c php truy cp. 
      if(localStorage.getItem('user')){
        return true;
      }
      // else{
      //   this.router.navigate(['']);
      //   return false;
      // }

      //cng nh else thi. localStorage undifined, th chuyn v login  login li.
      //dng navigate  nh dng li ng link.
      this.router.navigate(['login']);
      return false; //v tr v false  ko cho php cc path sd Guard ny truy cp.
    }

}
