/* Xem gii thch v cu hnh service ntn trong foldr ip2, c file ip.service.ts

- file ip.service.ts th l get, nn ko import Headers.
Cn  y l post nn c import thm Headers.

-Ging vi get:
+trong comp: import Http; import toPromise (angular 6 th ko cn)
+trong app.module.ts nh import HttpModule v gi xung imports: [] ( import trc  phn get r th thi)
+trong class ca comp: khai bo i tng http  post dl ln. angular s t ng new ngm cho i tng http .

-C cc ci mi so vi GET:
+c headers c ta ra t class Headers, class ny c ly t th vin @angular/http
+vi phng thc post() ta truyn cc loi: url, body l chui json, v bin header  to  trn.
sau  th cng toPromise v x l d liu  post ln.

*/ 

import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
//import 'rxjs/add/operator/toPromise'; //trong angular 6 th ko cn dng ny cng c th toPromise vi get http.

@Injectable()

export class SignIn3Service {

    constructor(private http: Http) { } //kb bin http t th vin Http, angular s t ng new ngm cho n thnh i tng ca Http. 

    //a request post ln server, sau  nhn response tr v, nn s c return.
    //Service s nhn tr v, sau  truyn qua comp bng cch comp gi service.
    sendPost(value){
        const url = 'http://localhost:3000/signin'; //3000 l do bn server ta quy nh, signin cng vy
        const headers = new Headers({ 'Content-Type': 'application/json'}); //header l thuc tnh OPTION c to ra ging nh ta choose header khi s dng postman  bi 50
        // const body = JSON.stringify(formSignIn.value);//body l 1 chui json.  y s i 1 javascript object thnh json, ri gi ln object c thuc tnh name='Khoa Phm'.
        //thay v formSignIn.value, th  y n nhn tham s value truyn vo l input t comp khi comp gi hm ny  post ln.
        const body = JSON.stringify(value);

        //gi phng thc  post ln, v return v gi tr c response v.
        return this.http.post(url, body, { headers }) //post ln
            .toPromise()  //server s tr response v. ta chuyn i dl tr v t server .
            .then(res => res.json()) // y gi nguyn object ln. nn .json ch ko .text

            // qua bn comp nhn dl ri mi log ra,  y ch nhim v ly dl thi.
            // .then(resJSON => console.log(resJSON));
    }
}