import { Component, OnInit, OnDestroy } from '@angular/core';

import { Product } from '../../folder-models/product';
import { ProductService } from '../../folder-services/product.service';

//
import { Router, ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';


/* hin th dng queryParams t comp: http://localhost:4200/products?name=&price= hin th ra ntn
-import Router
-Inject vo constructro nh service
-gi hm navigate  ly cc params t url hay t query nhp vo.

- ly c cc param v th dng Subscription  ly, ging nh bn router-product-detail.component.ts 
*/ 

@Component({
  selector: 'app-router-product-list',
  templateUrl: './router-product-list.component.html',
  styleUrls: ['./router-product-list.component.css']
})
export class RouterProductListComponent implements OnInit, OnDestroy {

  public products : Product[] = [];

  public name : string;
  public price : number;

  public queryParamsSubcription : Subscription;


  //
  constructor(
    private productService : ProductService, 
    private routerService : Router, 
    private activatedRoute : ActivatedRoute ) { }

  ngOnInit() {
    //nu ko sd activatedRoute bn di th c th ly products theo cch thng thng, cn nu sd activatedRoute th dng cch bn di
    // this.products = this.productService.getAllProducts();

    // sd ActivatedRoute  ly params v
    this.queryParamsSubcription = this.activatedRoute.queryParams.subscribe(data => {
      let name = data['name'];
      let price = data['price'];


      //gn li cc bin  thay gi gi tr trong khung input mi khi F5 li
      this.name = name;
      this.price = price;

      this.products = this.productService.getAllProducts(name, price);
    });
  }


  ngOnDestroy(){
    if(this.queryParamsSubcription){
      this.queryParamsSubcription.unsubscribe();
    }
  }


    //gi hm navigate nh dng params truyn i,  hin th ln url.
    //y l cch thit lp ng link, nn cn ch , phi thit lp trn ng dn hin ti.
    //vd: lc ny ang  /products/list mun thit lp link th phi bt u bng /products/list, ri mi ti cc tham s.
  onSearch(){

    //TH router ch n products, ko c tham s con: th ko cn khai bo tham s con trong [] nh TH di
    this.routerService.navigate(['/products/list'], { queryParams: {name: this.name ? this.name : '', price: this.price ? this.price : ''} });
    // console.log(this.name);


    //TH nu router c tham s con th phi khai bo trong [ , 'id']
    //http://localhost:4200/products/id?name=&price=
    // this.routerServce.navigate(['/products', 'id'], { queryParams: {name: this.name ? this.name : '', price: this.price ? this.price : ''} });
  }

}



