/*Bi 40: CanDeactivate
L ngc li vi CanActive
-l service cho php ngi dng thot khi path router  hay khng
-c php tng t canactive

-hm canDeactivate tr v:
+true : c th thot ra
+false: ko th thot ra.

---------
THC HIN

-v CanDeactive cng l 1 service, nn to fodel guards trong folder services, ln ny to service lm nhim v candeativate.
-Vo cli, xem c php: teminal: "ng g guard access" (my-new-guard)  to nhanh file: access.guard.ts

+default file to ra l dng canactive,  nn phi sa li tn v cc import thnh dng candeactive v xa nhng import ko cn.

+candeactive yu cu truyn vo comp cn sd service candeactive ny trong implement trong access.guard.ts vi <comp sd>.


- y sd candeactive cho comp Router-Home, m comp ny c module ring l router-home.module.ts, nn ta import file service
access.guard.ts vo Home module, vo ko xung providers thm vo.

-gi canDeactivate: [AccessGuard] : cho lin kt path no mun gi li, lin k path trong router-home.module.ts.
nu AccessGuard tr v true th c php thot ra, cn false th ko c php thot ra.

- iu chnh true/false ca access.guard.ts, thng qua comp router-home th phi dng 1 bin ton cc(c h thng u dng c): l bin localStorage(key: value(string)), bin ny s c set,get gi tr theo key, all comp u sd c bin ny.

-bin localStorage s lu li gi tr vi key c set, vy nn mun thay i trng thi sau mi ln F5 th phi hy bin , hoc l xa cokie ca trnh duyt.

*/

import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-router-home',
  templateUrl: './router-home.component.html',
  styleUrls: ['./router-home.component.css']
})
export class RouterHomeComponent implements OnInit {

  error = 0;
  success = 0;

  constructor() { }

  ngOnInit() {
    //mi ln gi n comp ny th n li remove key 'key'
    localStorage.removeItem('key');    
  }

  onAccess(key: string){
    //sd bin ton cc localStorage, mi comp u dng c, ging nh bn CanActivate trong file: router-login.component.ts
    //Dng bin ny  set gi tr theo key: value(string) cho service access.guard.ts.
    // localStorage.setItem('key', key); //key sau: l value set vo cho bin localStorage vi key l 'key'

    //nu key nhp vo ='123456' th mi cho php thot.
    if(key === '123456'){
      localStorage.setItem('key', key); //key sau: l value set vo cho bin localStorage vi key l 'key'
      this.success = 1;
    }else{
      //ngc li nu ngi dng cha login th gn -1
      this.error = -1;
    }

  }

}
