import { Component } from '@angular/core';

@Component({
    //vo https://angular.io/api/ ti  Filter search tm Component, chn @Component s ra link https://angular.io/api/core/Component
    //Theo , styles: l mng nn phi [].
    //Vo  xem nh dng  lm cho ng theo tng ci.

    // template: '<h3>this is world component</h3>',
    templateUrl: './word.component.html', //Dng string bt. File Url th gi ng dn ch ko chn trc tip.
    selector: 'app-world', //to tn th ca component khi gi trong file app.component.html: <app-world></app-world>
    
    //  chui c xung nhiu dng th t chui trong `` (trn phm Tab)
    
    // styles: [`  
    //     h3 {
    //         color: red;
    //     }
    // `]

    styleUrls: ['./word.component.css'], //Dng mng. file Url th gi link, ch ko chn trc tip.
})

export class WorldComponent {
    en: string = 'Hello'; //to bin, bn html gi bng cch {{en}}
    vn = 'Xin cho';    //type script t hiu kdl, c string hay ko u c.

    imageUrl = 'https://angular.io/assets/images/logos/angular/logo-nav@2x.png'; //to bin imageUrl

    forgot = false; //to bin forgot

    //to function  gi khi c event yu cu
    toggleForgot(){
        this.forgot = !this.forgot;
    }
}  
