import { Pipe, PipeTransform } from '@angular/core';
import { forEach } from '@angular/router/src/utils/collection';

@Pipe({
  name: 'evenOdd'
})
export class EvenOddPipe implements PipeTransform {

  transform(arrNumber: number[], evenValue: number): any {
    // var result : number[]; //mng tnh, ko th dng push c, 
    var result = new Array(); //mng ng, dng push c.

    //vo y  xem cc hm ca array trong typescript: 
    //https://www.tutorialspoint.com/typescript/typescript_arrays.htm

    //hm push() thm phn t v cui mng, tr v length mi.

    if (evenValue === 1) {
      arrNumber.forEach((item, index) => {
        item % 2 === 0 ? result.push(item) : -1; //nu true th tr v length mi, false th -1 hay s no cng c, cho c ngi l c. 
      });
    }else{
      arrNumber.forEach((item, index) => {
        item % 2 !== 0 ? result.push(item) : -1; //nu true th tr v length mi, false th -1 hay s no cng c, cho c ngi l c. 
      });
    }

    return result;
  }

}
