import { Component, OnInit } from "@angular/core";
import { MovieService } from '../../folder-services/movie.service';
import { Movie } from '../../folder-models/movie';



@Component({
  selector: "app-movie-list",
  templateUrl: "./movie-list.component.html",
  styleUrls: ["./movie-list.component.css"]
})
export class MovieListComponent implements OnInit {
  
  public movies : Movie[] = [];

  constructor(private movieService : MovieService) { }

  ngOnInit() {
    this.movies =  this.movieService.getAllMovie();
    console.log(this.movies);
  }

  //hm xa nu double click vo hng : double click s gi id v y, ri hm ny s gi id qua service  xa
  deleteVideo(id: number){
    // console.log(id);
    this.movieService.deleteVideo(id);
  }


  //UPDATE
  //kch n 1 ln s hin ra khung  update
  isCheckUpdate = false;
  id: number;
  name: string='';
  link: string='';
  author: string='';

  //x l hin ln khung update v binding dl ra  input
  //Nhn cc name c gi v thng qua ngFor
  enableUpdateVideo(id: number, name: string, link: string, author: string){
    this.isCheckUpdate =  !this.isCheckUpdate; //khung hin ln
    //cc gi tr nhn v
    this.id = id;
    this.name = name;
    this.link = link;
    this.author = author;
    // console.log(id, name, link, author,  this.isCheckUpdate);
  }


  //x l update
  //Nhn cc name mi gi v, thng qua #nameRef l template reference variable
  handleUpdateVideo(name: string, link: string, author: string){
    let movie = new Movie(this.id, name, link, author);
    this.movieService.updateVideo(movie);

    //update xong th tt form i v remove id li
    this.isCheckUpdate = false;
    this.id = null;
  }

}
