import { Component } from '@angular/core';


/* ngContent: phn vng   dl vo. D liu c a vo i km mi khi gi comp ny ra. 

vd:trong app.comp.html s gi comp ny ra v truyn dl vo (NHNG KO PHI TRUYN THEO KIU @INPUT, M L TRUYN <comp>DL  GIA <comp> TH NTN)
    
<com ny NU TRUYN THEO @INPUT TH PHI TRUYN TRONG NY>dl a vo mi khi gi n ra  comp khc</com ny>

- Ch truyn lun vo class chnh, truyn vo 1 ch :
 y ta truyn <ng-content></ng-content> trong class="card" l class to 1 khung bc xung quanh.

Lc ny trong app.component.html(page chnh), ta khai bo comp ny: <app-card><p>Khoa phm training</p></app-card> 
    vi dl truyn vo l Khoa phm training, d liu truyn vo  s c a vo v tr <ng-content></ng-content>, nn n s c nm trong khung bc.

-ngContent: c tnh cht ti s dng khung vin  m ko cn phi khai bo li. Ch cn gi comp  ra v truyn dl vo l c.

-Truyn vo cc class khc nhau, truyn vo nhiu ch th ta dng select  phn bit class cn truyn vo:
+phn bit bng select .card-header hay .body-header

vd: bn app.comp.html: <p class="card-body">Khoa phm: nhiu ch-vo body</p> s truyn vo v tr <ng-content> trong class body.

*/ 

@Component({
  selector: "app-card",
  template: `
        <div class="card">
            <h1>ng-content nhn nhng ci ko k no c</h1>
            <ng-content></ng-content>
            <br>

            <h1>ng-content nhn theo k class</h1>
            <ng-content select=".card-header"></ng-content>
            <br>
            <ng-content select=".card-body"></ng-content>
            <br>
            <ng-content select=".myClass"></ng-content>
            <br>

            <h1>ng-content nhn theo k th h4</h1>
            <ng-content select="h4"></ng-content>
            <br>

            <h1>ng-content nhn theo k th (div myAttr), div hay p hay h u c </h1>
            <ng-content select="[myAttr]"></ng-content>
            <br>

            <h1>ng-content nhn theo k th (span course="angular6"), span hay div hay p u c</h1>
            <ng-content select="[course=angular6]"></ng-content>
            <br><br>

            <h1>Multi Attribute: ng-content nhn theo k th (p handsome demo) </h1>
            <ng-content select="[handsome][demo]"></ng-content>
            <br>

        </div>
    `,
  styles: [
    `
      .card {
        padding: 5px;
        margin: 5px;
        background: #fff;
        border-radius: 2px;
        display: inline-block;
        width: 300px;
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19),
          0 6px 6px rgba(0, 0, 0, 0.23);
      }
    `
  ]
})
export class CardComponent {}