import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
//import 'rxjs/add/operator/toPromise'; //trong angular 6 th ko cn dng ny cng c th toPromise vi get http.

@Injectable()


/*To file ny cha service:
-import:  Injectable t core, Http t http.
-Nu l angular4 th phi import 'rxjs/add/operator/toPromise':  s dng toPromise(), cn angular  t ng c ko cn import.

-V y l service nn gi @Injectable()  n thc thi service, ging component th gi @Component(){}

-Xung di phn son service: class IpService:  lm vic vi service.
hm getIp s return v ip ly t server thng qua service.

*/ 


export class Ip2Service {

    constructor(private http: Http) { } //kb bin http t th vin Http, angular s t ng new ngm cho n thnh i tng ca Http. 

    //Ly service ra.  y l tr v ip nn nh c return.
    getIp(){
        return this.http.get('http://ip.jsontest.com/') //ly dl t server ny v, tr dl v kiu Observable
        .toPromise() // chuyn dl sang dng Promise.  c toPromise() th phi import  trn cng (Vi Angular6 th ko cn import na).
        .then(res => res.json()) //then s nhn gtri tr v, chuyn promise sang kiu json.
        .then(resJson => resJson.ip) //tr v ip ca json, ip ly t service. (ng vi tn gi getIp() ca function ny)

        // y then: nhn gi tr tr v, v catch phng ngoi l.
    }
}