import { Component, Input, OnChanges, OnInit, DoCheck, OnDestroy, SimpleChanges, AfterContentInit, AfterContentChecked, AfterViewInit, AfterViewChecked } from '@angular/core';

@Component({
  selector: 'app-lifecycle-hooks',
  templateUrl: './lifecycle-hooks.component.html',
  styleUrls: ['./lifecycle-hooks.component.css']
})
export class LifecycleHooksComponent implements OnChanges, OnInit, DoCheck, AfterContentInit, AfterContentChecked, AfterViewInit, AfterViewChecked, OnDestroy {

  public products : any[] = [];

  @Input() total: Number = 0;

  public previousTotal : number;

  /* ko phi hook, nhng c gi u tin, thng dng  tim cc Dependency Injection nh cc Service.
  */
  constructor() { 
    // console.log('Constructor()');
  } 


  /*
  -ngOnChanges: c thc hin khi @INPUT c s thay i, c qun l thng qua i tng SimpleChanges, c gi trc c ngOnInit().
  -mi ln nhn gi tr input gi t app.comp.html gi v th hm ngOnChanges() c thc thi, dng  x l khi @input c s thay i.
  
  -cho ta 1 i tng kiu SimpleChanges (SimpleChanges phi import t @Angular/core).
  -object SimpleChanges c cc thuc tnh: 
	+currentValue: gi tr hin ti,
	+previousValue: gi tr trc ,
	+firstChange: thay i ln u tin: true/false.

  Console.log v F12 ta thy, sau mi ln input thay i th: s cho ra object total:simpleChanges vi total l tn input nhn s thay i,
    v object  cha y  cc thuc tnh nh trn. */ 
  ngOnChanges(simpleChanges: SimpleChanges) {
    // console.log('ngOnChanges()');
    // console.log(simpleChanges);
    this.previousTotal = simpleChanges.total.previousValue;
  }



  /* chy ngay sau khi ngOnChanges() c gi ln u tin v ch chy 1 ln duy nht.
  Dng  khi to cc gi tr, cc thuc tnh u vo ca comp.
  */
  ngOnInit() {
    // console.log('ngOnInit()');
    this.products = [
      {
        id: 1,
        name: 'Iphone 7',
        status: true
      }
    ];
  }


  /*ngDoCheck: c mi ln gi ngOnChanges() HOC ngOnInit() th hm ngDoCheck() s c gi theo.  */
  ngDoCheck() {
    // console.log('ngDoCheck()');
  }


  /*ngAfterContentInit, ngAfterContentChecked, ngAfterViewInit, ngAfterViewChecked:
  l cc hook bn trong ngDoCheck*/ 

  /*ngAfterContentInit: S dng ng-content  kim tra, s dng ContentChild (kiu ElementRef) -template reference variable
  Ch c gi 1 ln duy nht. Ch dnh cho component*/ 
  ngAfterContentInit(){
    // console.log('ngAfterContentInit()');
  }
  //c gi nhiu ln. Ch dnh cho component.
  ngAfterContentChecked(){
    // console.log('ngAfterContentChecked()');
  }


  /*ngAfterViewInit: c gi sau khi khi to view ca comp v cc child views.
  Ch : template variable #tenbien cng l sd child view.
  Ch c gi 1 ln.*/
  ngAfterViewInit(){
    // console.log('ngAfterViewInit()');
  }
  //ngAfterViewChecked: c gi nhiu ln. Ch dnh cho comp.
  ngAfterViewChecked(){
    // console.log('ngAfterViewChecked()');
  }


  /*Hm ny ch thc thi khi comp ang hin ri b n i, mi ln n comp i l n li c thc thi.
  S dng ngIf kt hp vi bin isDestroy trong app.component.html gip vic n hin comp c thc hin mi ln n button.*/ 
  ngOnDestroy() {
    // console.log('ngOnDestroy()');
  }

}
