import { Component, OnInit, Output, EventEmitter} from '@angular/core';

@Component({
    selector: 'app-child',
    template: `
                <ng-content></ng-content>
                <br>

                <button (click)="addForParent();">Add</button>
                <button (click)="subForParent();">Sub</button>
                <br><br>
                <h3>{{ value2 }}</h3>
    `
})

export class ChildComponent implements OnInit {

    value2 = 0;

    constructor() { }

    ngOnInit() { }


    /*Mun gi s kin t child qua parent th phi:
    Import EventEmitter, Output  dng trn cng
    Khai bo s kin phi c @Output() eventname = new EventMiter().
    Sau khi khai bo y , mun kch hot myclick  c th gi s kin i: this.myclick.emit()

    event myClick  c to  y, qua parent ta ch cn gi n myClick: <app-child (myClick)="value = value + 1"></app-child>
        +child.comp.ts gi (myClick) n parent.comp.ts, nhng lc ny myClick cha c tc dng v cha c kch hot.
        +ti child.comp.ts, mi ln kch btn Add th myClick li c kch hot(gi n myClick.emit()), nn (myClick) bn parent mi c tc dng v value bn parent s ++ ln.

    =>Tm li:  y bm Add th kch hot myClick, lm cho myClick c gi bn parent.ts c tc dng dn n value++.
    */

    /*bi 28: Ouput c tham s
    -TH  parent.ts cn nhn tham s t child.ts truyn v:
    +V tr nhn tham s  parent.ts phi truyn vo LUN l tham s $event
    + child mi khi xc nhn s kin myClick th truyn km theo tham s
    + @Output ch new EventEmitter<boolean> phi truyn kiu dl vo,  khng nh ng kdl cn truyn qua bn parent,  y khng nh tham s truyn qua l boolean.
    (nu ko c <boolean> th n s truyn qua bt k ci g m s ko bo li)
    
    vd:  child: this.myClick.emit(true); 
         parent: changeValue($event);
        =>th l truyn tham s true  child qua tham s $event  parent mi khi myClick c kch hot.

    */


    //to ra keyname ng thi l tn bin s kin output, km theo <kiu dl tham s cn truyn qua parent2>, nu ko bit kdl g th  <any>.
    // @Output() myClick = new EventEmitter<any>();
    @Output() myClick = new EventEmitter<boolean>();

    //cch khc: truyn keyname !==/=== tenbienOutput (bin th sd cho child comp, cn keyname l  gi bn parent comp)
    // @Output('myClickKey') myClick = new EventEmitter<boolean>();


    //kch hot s kin output
    addForParent(){
        this.myClick.emit(true);
    }

    subForParent(){
        this.myClick.emit(false);
    }
} 