/* nhn id,name,phoneNumber t app-routing.module.ts vo component ny.

-import ActivatedRoute, ParamMap t router
-trong constructor s khi to bin cho ActivatedRoute
-Vo ngOnInit(): dng ParamMap  get cc bin truyn vo. vd params.get('id') l ly gi tr bin id truyn qua t app-routing.module.ts.
*/

import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router'; 

@Component({
  selector: 'app-contact-detail',
  templateUrl: './contact-detail.component.html',
  styleUrls: ['./contact-detail.component.css']
})

export class ContactDetailComponent implements OnInit {
  name = '';
  phoneNumber = '';

  constructor(private route: ActivatedRoute) { }

  ngOnInit() {
    this.route.paramMap.subscribe((params: ParamMap) => {
      const id = params.get('id'); //get id truyn qua t app-routing.module.ts

      this.name = params.get('name');
      this.phoneNumber = params.get('phoneNumber');

      console.log(id, this.name, this.phoneNumber);
    });
  }

}
