import { Component, OnInit, OnDestroy } from '@angular/core';

// ly params, c th l ly id cn edit t url: http://localhost:4200/products/edit/3
//c id th gi ti hm c chc nng edit ca service
import { ActivatedRoute, Params } from '@angular/router'; 

//s dng i tng Subscription  lng nghe params trn link, nn phi to ra v hy n i.
import { Subscription } from 'rxjs';

import { ProductService } from '../../folder-services/product.service';
import { Product } from '../../folder-models/product';



@Component({
  selector: 'app-router-product-edit',
  templateUrl: './router-product-edit.component.html',
  styleUrls: ['./router-product-edit.component.css']
})
export class RouterProductEditComponent implements OnInit, OnDestroy {

  public product : Product = null;
  public subcription : Subscription = null;

  
  constructor(private activatedRoute : ActivatedRoute, private productService : ProductService) { }

  //mi ln router ti ng dn path: 'edit/:id' th s t ng n component ny, theo cu hnh trong file router-products.module.ts,
  //do vy, mi ln router ti th comp c gi, v ngOnInit cng c gi. S ly c id t link edit.
  ngOnInit() {
    this.handleParams();
  }


   //hy i tng subcription mi ln kt thc comp.
   ngOnDestroy() {
    if(this.subcription){
      this.subcription.unsubscribe();
    }
  }


  //Hm ly params.
  // ly params, c th l ly id cn edit t url: http://localhost:4200/products/edit/3
  //c id th gi ti hm c chc nng edit ca service
  handleParams(){
    //v param bn path: 'edit/:id' ch mi id, nn lc ny id chnh l params
    this.subcription = this.activatedRoute.params.subscribe((params: Params) => {
      // console.log(params.id);
      //params.id : l tham s id cn ly

      let id = (+params.id);  // (+)  chuyn v kdl number, trnh li xung t kdl
      this.product = this.productService.getProductByID(id); //ly c product vi id tng ng,  hin th cc thnh phn ln cc  thng qua ngModel
    });
  }

  //lu li cc thng tin  edit
  handleSaveEdit(){
    this.productService.updateProduct(this.product);
  }

}
