
//PIPE: Ly 1 on vn bn + ...: nu vn bn di qu, m mun hin th ln th ta ch ly 1 on thiri + thm du ...

import { Pipe, PipeTransform } from '@angular/core';

//pipename formatData s c gi trong html  gi ra tnh cht ca pipe.
@Pipe({
  name: 'formatData'
})

//pipe ny dng  ct chui, tng t pipe slice c sn.
export class FormatDataPipe implements PipeTransform {

  //any ngha l kiu dl bt k no , thng l dng cho object
  //kiu any trong () l kiu value nhn vo, any bn ngoi l kiu value tr ra. 
  transform(value: any, args?: any): any {
    // console.log(typeof args); // xem args l kiu dl g

    return value.substr(0, 10) + '...'; //ct t v tr 0 n 10-1
  }

}
