import { Component, OnInit, OnDestroy } from '@angular/core';

import { Router, ActivatedRoute } from '@angular/router';

//s dng i tng Subscription  lng nghe params trn link, nn phi to ra v hy n i.
import { Subscription } from 'rxjs';

import { ProductService } from '../../folder-services/product.service';
import { Product } from '../../folder-models/product';




@Component({
  selector: 'app-router-product-detail',
  templateUrl: './router-product-detail.component.html',
  styleUrls: ['./router-product-detail.component.css']
})

/*Ly Router params thng qua ActivatedRoute:

**Cch 1: ly v i tng snapshot t i tng activatedRoute,
-import ActivatedRoute t @angular/router
-tin hnh Inject nh 1 service
-ly params t snapshot
=>  dng cch ny th trong ngOnInit() gi hm: handleParamsRouteBySnapshot()

**Cch 2: ly v i tng subcription t i tng activatedRoute,
-import ActivatedRoute t @angular/router
-import Subscription t rxjs
-tin hnh Inject nh 1 service
-Khai bo i tng subcription  nhn subcription tr v t activatedRoute, ly params thng qua subscribe.
=> dng cch ny th trong ngOnInit() gi hm: handleParams()

-ti comp nhn tham s ny, nh hy i tng subcription mi ln kt thc comp, vo trong ngOnDestroy m hy.
(Log xong F12  xem cc thnh phn trong ActivatedRoute)
*/

export class RouterProductDetailComponent implements OnInit, OnDestroy {

  public product : Product = null;
  public subcription : Subscription;


  //Inject vo constructor ging nh 1 service
  constructor(
    private activatedRoute : ActivatedRoute, 
    private productService : ProductService,
    private routerService : Router) { }




  /*mi ln click ng router th n s gi n comp product detail ny, th hm ngOnInit s c gi, lc ny ta s ly params lun. 
  (Cc ln router mang n vi cc tham s khc nhau, nhng mi ln n th comp ny th s gi li hm ly tham s v mi ln n 
  th comp li c khi ng th hm ngOnInit li c gi.)
  Vy nn cc hm ly tham s s c gi trong ngOnInit*/
  ngOnInit() {
    // this.handleParamsRouteBySnapshot();
    this.handleParams();
  }
  

  //hy i tng subcription mi ln kt thc comp.
  ngOnDestroy() {
    if(this.subcription){
      this.subcription.unsubscribe();
    }
  }

  
  //Cch 1: ly v i tng snapshot t i tng activatedRoute,
  handleParamsRouteBySnapshot(){
    // console.log(this.activatedRoute);
    // console.log(this.activatedRoute.snapshot.params['id']);
    let id = (+this.activatedRoute.snapshot.params['id']); // (+)  chuyn v kdl number, trnh li xung t kdl
    this.product = this.productService.getProductByID(id);
  }

  //Cch 2: ly v i tng subcription t i tng activatedRoute
  handleParams(){
    this.subcription = this.activatedRoute.params.subscribe(data => {
      let id = (+data.id);  // (+)  chuyn v kdl number, trnh li xung t kdl
      this.product = this.productService.getProductByID(id);

      // console.log(this.product);
    });
  }


  //gi hm navigate  nh hng router mun truyn i.
  //vo router-products.component.ts  hiu r hn v navigate
  onBackToList(){
    //Cch 1: a ng dn trc tip,  li v
    // this.routerService.navigate(['products/list']);

    //Cch 2: sd relativeTo. Gi ti ng dn trc n 1 bc, ri + thm ng dn cn +
    //ban u http://localhost:4200/ khi kch vo hng 1 ca bng th + products/1(ng vi router ca detail) th ra  http://localhost:4200/products/1, 
    //th activatedRoute chnh l http://localhost:4200/products/1, .parent  quay v 1 bc trc http://localhost:4200/ + products/list t this.routerService.navigate truyn vo.
    this.routerService.navigate(['products/list'], {
      relativeTo : this.activatedRoute.parent //gn dng key: value //http://localhost:4200 + /products/list
    });
  }
  


  //cu hnh ng link khi nhn nt edit. 
  //khi nhn btn edit cho thnh phn detail 1 th th link s c cu hnh thnh: 
  //  http://localhost:4200/products/edit/1, ta phi cu hnh  ng dn n ra c nh vy.
  onEdit(){
    //ang  http://localhost:4200/products/1, bm edit th v http://localhost:4200/products/edit
    // this.routerService.navigate([`products/edit/${this.product.id}`], { //v c bin $ nn i thnh du `` thay v du '' nh  trn
    this.routerService.navigate([`products/edit`, this.product.id], {  //HOC CCH KHC  GI product.id
      relativeTo : this.activatedRoute.parent //gn dng key: value //http://localhost:4200 + /products/edit
    });

  }


  onDelete(){
    // console.log(this.product.id);
    this.productService.deleteProduct(this.product.id);
  }

}
