
//PIPE: lc cc phn t trong mng ra theo iu kin

import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filter'
})
export class FilterPipe implements PipeTransform {

  transform(products: any[], idFilter : string,  nameFilter : string, statusFilter : string ): any {
    // console.log(idFilter, nameFilter, statusFilter);
    // return products;

    /*
    -dng undifine cho c string v nhiu kdl khc boolean:
    nu l boolean:   status: boolean th (status)=true v (!status)=false

    nu l kdl khc boolean th l difine:c dl v undifine:null
    status: string th (status)=difine=!null v (!status)=undifine=null
    */
    if(!idFilter && !nameFilter && !statusFilter){
      return products; //nu cha nhp vo g ht th ko cn lc, tr v nguyn array products
    }else{
      //nu string idfilter=define ngha l !=null, th lc mng bng hm filter() c sn.
      //indexOf(id): tr v v tr chui id trong mng/chui 0->n-1, tr v v -1 nu ko tm ra.
      
      // dng c indexOf(idFilter) th phi chuyn id ca array element v kiu string.
      //x l 1 phn t trong mng products, x.id: th l id trong array cn phi toString(), 
      //cn indexOf(idFilter) l idFilter nhp vo  lc, vd: id trong array l 1234 th ch cn nhp vo idFilter=1 l n  hin ra danh sch cc id c '1' ri.
      if(idFilter){
        products = products.filter(x => {
          //nu != -1 th return true; phn t  s c duyt, lc ln v a ra danh sch.
          return x.id.toString().indexOf(idFilter) != -1;
        });
      }


      //lc name xong r, th vn c th lc status na, nn ko dng else if m ch dng if 
      //Nu string namefilter=define ngha l !=null, th lc mng bng hm filter() c sn.
      
      //i vi chui th cn a v ton b ch thng, hoc ton ch hoa  cng loi mi so snh c.
      //  id  trn cng nhp vo chui, nhng ko cn ti v id l nhp vo chui S.

      // y name ca element array  l string ri, nn ko dng toString(), ch cn a v thng hoc hoa l c.
      if(nameFilter){
        products = products.filter(x => {
          //nu != -1 th return true; phn t  s c duyt, lc ln v a ra danh sch.
          return x.name.toLowerCase().indexOf(nameFilter.toLowerCase()) != -1;
        });
      }

      //Nu string statusFilter=define ngha l !=null, th lc mng bng hm filter() c sn.
      //statusFilter='true'/'false'/'all', nu bng 'true'/'false' th n lc, 
      //  cn ='all' th n ko lc m s ly tt.

      //v status trong array l boolean nn phi toString()  dng c indexOf()
      if(statusFilter){
        products = products.filter(x => {

          // console.log(x.status.toString().toLowerCase()); //tr v string 'true', 'false'

          //nu != -1 th return true; phn t x  s c duyt, lc ln v a ra danh sch.
          //nu ==-1 th return false; theo hm filter() th false s ko c duyt v s b loi.
          // return x.status.toString().toLowerCase().indexOf(statusFilter.toLowerCase()) != -1; 

          //nu statusFilter='all' th return true: hm filter() quy nh true ngha l phn t x c duyt v a vo danh sch.
          //  ko cn phi xt thm iu kin g na, nn nu statusFilter='all' th mi phn t 
          //  trong products hin ti s c duyt(products hin ti l products  lc qua id,name).

          //ngc li th s lc theo true/false ca x.status ca array products.
          
          return statusFilter.toLowerCase() === 'all' ? true : x.status.toString().toLowerCase().indexOf(statusFilter.toLowerCase()) != -1; 
        });
      }

      return products;
    }

  }

}
